import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
  data() {
    return {
      imgIndx: 0,
      shortVideoSrc: require('../assets/frame1.mp4'),
      longVideoSrc: require('../assets/animation1.mp4')
    }
  },
  methods: {
    getPath() {
      let index = this.imgIndx
      if (index < 10) {
        index = '0' + index
      }
      return require('../assets/img/01_微动'+index+'.jpg')
    },
    changeIndx(timer=50) {
      console.log('开始啦---imgIndx=', this.imgIndx)
      setTimeout(() => {
        if (this.imgIndx < 90) {
          this.imgIndx++
        } else {
          this.imgIndx = 0
        }
        this.changeIndx()
      }, timer)
    }
  },
  mounted() {
    this.$nextTick(function () {
      this.changeIndx()
    })
  }
}



