  import { mapGetters } from 'vuex'
  import html2canvas from 'html2canvas'
  import TouchUtils from '../utils/touchUtils'
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';

  export default {
    computed: {
      ...mapGetters([
        'userInfo'
      ])
    },
    data() {
      return {
        showSaveImg: false,
        mySwiper: null,
        dataUrl: null,
        currentIndex: 0,
        swiperData: [
          {
            url: 'icon_science_1.png',
            saveImg: 'icon_save_1.png',
            title: '是血脉的联系  也是隐患的深藏',
            desc: '5%-10%乳腺癌的发生都与遗传基因或基因突变相关'
          },
          {
            url: 'icon_science_2.png',
            saveImg: 'icon_save_2.png',
            title: '星辰初启  莫将韶华湮灭在黑暗中',
            desc: '一线城市的乳腺癌患病率日趋年轻化<br>长期熬夜、饮酒、压力大都是诱因'
          },
          {
            url: 'icon_science_3.png',
            saveImg: 'icon_save_3.png',
            title: '母爱如大海  愿绵延安康',
            desc: '35岁后生育，患乳腺癌的风险是25岁前生育的3倍'
          },
          {
            url: 'icon_science_4.png',
            saveImg: 'icon_save_4.png',
            title: '坚如磐石  也有让其顷刻崩塌的裂痕',
            desc: '男性一生中患乳腺癌的风险大约为1/1000'
          }
        ],
        startx: null,
        starty: null,
        endx: null,
        endy: null
      }
    },
    methods: {
      touchStart(e) {
        console.log('touchStart---')
        this.startx = e.touches[0].pageX;
        this.starty = e.touches[0].pageY;
      },
      touchEnd(e) {
        console.log('touchEnd---')
        this.endx = e.changedTouches[0].pageX;
        this.endy = e.changedTouches[0].pageY;
        var direction = TouchUtils.getDirection(this.startx, this.starty, this.endx, this.endy)
        switch (direction) {
          case 0:
            console.log("未滑动！");
            break;
          case 1:
            console.log("向上！")
            this.$router.push({path: '/ending'})
            break;
          case 2:
            console.log("向下！")
            break;
          case 3:
            console.log("向左！")
            break;
          case 4:
            console.log("向右！")
            break;
          default:
        }
      },
      jump2Next() {
        console.log('点击了--')
        this.$router.push({path: '/ending'})
      },
      getPath(name) {
        return require('../assets/' + name)
      }
    },
    mounted() {
      this.$nextTick(function () {
        let _myThis = this;
        this.mySwiper = new Swiper('.swiper-container', {
          direction: 'horizontal',
          loop: true,
          slidesPerView: "auto",
          centeredSlides: true,
          spaceBetween: 0, // 两张图片的间距
          on: {
            slideChange: function () {
              _myThis.currentIndex = this.activeIndex % 4;
            },
          },
        })
//        html2canvas(document.querySelector('#captureId')).then(canvas => {
//          let imgUrl = canvas.toDataURL('image/png')
//          this.dataUrl = imgUrl
//          console.log('生成图片成功')
//        })

      })
    }
  }



