
import { mapGetters } from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XButton, XHeader
},
computed: {
...mapGetters([
'sidebar',
'language'
])
},
data() {
return {}
},
methods: {
jump2Next() {
this.$router.push({path: '/swiper-info'})
}
},
mounted() {
this.$nextTick(function () {

})
}
}



